import {observable, decorate} from 'mobx';

export class TodoItem {
    id = new Date();
    title;
    isDone;

    constructor(title, isDone = false) {
        this.title = title;
        this.isDone = isDone;
    }
}

decorate(TodoItem, {
    title: observable,
    isDone: observable,
})

export default TodoItem
